import { getCookie } from "../utils/cookie";
import axiosInstance from "./axiosInstance";

const createGiftCard = (cardData) => {
  const token = getCookie("accessToken");
  return axiosInstance.post("/createGiftCard", cardData, {
    headers: { authorization: `Bearer ${token}` },
  });
};

const getGiftCards = () => {
  const token = getCookie("accessToken");
  return axiosInstance.get("/getGiftCardByUser", {
    headers: { authorization: `Bearer ${token}` },
  });
};

const getAllTemplates = () => {
  return axiosInstance.get("/getAllTemplates");
};

export { createGiftCard, getGiftCards, getAllTemplates };
