import { useState, useEffect, useCallback } from "react";
import {
  getGiftCards,
  createGiftCard,
  getAllTemplates,
} from "../api/giftCardApis";

const useGiftCard = (templateId) => {
  const [giftCards, setGiftCards] = useState([]);
  const [templates, setTemplates] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchGiftCards = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "giftCards";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setGiftCards(JSON.parse(cachedData));
      } else {
        const response = await getGiftCards();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(cacheKey, JSON.stringify(response.data));
          setTemplates(response.data);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, []);

  const fetchAllTemplates = useCallback(async () => {
    if (templateId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "giftCardTemplates";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setTemplates(JSON.parse(cachedData));
      } else {
        const response = await getAllTemplates();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.allTemplates)
          );
          setTemplates(response.data.allTemplates);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [templateId]);

  const addGiftCard = useCallback(async (cardData) => {
    setLoading(true);
    setError(null);
    try {
      const response = await createGiftCard(cardData);

      if (response?.status) {
        console.log(response.data);
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchGiftCards();
  }, [fetchGiftCards]);

  useEffect(() => {
    fetchAllTemplates();
  }, [fetchAllTemplates]);

  return {
    giftCards,
    templates,
    addGiftCard,
    loading,
    error,
    refresh: fetchGiftCards,
  };
};

export default useGiftCard;
